import React, { Component } from 'react';
import { Grid } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import AllQuiz from './AllQuiz';
import AttemptedQuiz from './AttemptedQuiz';
import AddQuiz from '..//AdminUser//AddQuiz'
const useStyles = makeStyles((theme) => ({
    mainView: {
        ['@media (max-width:600px)']: { // eslint-disable-line no-useless-computed-key
          margin: 'auto',
          marginTop:5,
        }
    },

}));
export default function MainView(props){
      const classes = useStyles();
      console.log("Prop.index is "+props.index)
      let content;
      if(props.index===2)
      {
        console.log("getting attempted quizes...")
        content =
        <Grid container item xs={12}>
        <AttemptedQuiz/>
        </Grid>
      }
      else if(props.index===3)
      {
        console.log("getting add quiz...")
        content =
        <Grid container item xs={12}>
        <AddQuiz/>
        </Grid>
      }
      else
      {
        console.log("getting all quizes...")
        content =
        <Grid container item xs={12}>
        <AllQuiz/>
        </Grid>
      }
      return (
        <Grid className={classes.mainView} container xs={12}>
          {content}
        </Grid>
      );

}
