import React, {useEffect} from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import { Grid, Box } from '@material-ui/core';
import {Link} from 'react-router-dom';
import { useHistory } from "react-router-dom";
import {baseUrl} from '..//../properties';
import {unstable_batchedUpdates} from 'react-dom';
import CircularProgress from '@material-ui/core/CircularProgress';
import quizImg from '..//..//images//quizImg.jpg'
//const baseUrl = "http://127.0.0.1:8586";

//const baseUrl = "https://myexamportal-backend.herokuapp.com/"

const useStyles = makeStyles({
  root: {
      width:'80%',
      maxWidth:'400px',
      boxShadow: "1.8px 3.5px 3.5px hsl(0deg 0% 0% / 0.45)",
      transition: "0.25s ease-in-out",
      justifyContent:"space-around",
      alignItems:"center",
      margin:"20px",
      spacing:20,
      borderRadius: 15,
      ['@media (max-width:600px)']: { // eslint-disable-line no-useless-computed-key
        margin: 'auto',
        marginTop:20
      },
        '&:hover': {
          boxShadow: "10px 10px 10px 2px  hsl(0deg 0% 0% / 0.45)",
          //transform: "scale(1.1)",
          transition: "0.25s ease-in-out",
      },
  },

  title: {
    fontSize: 14,
  },
  cars:{
    justifyContent:"space-around",
    alignItems:"center",
    margin:"auto",
    padding:"auto",
  }
});



const Quizes = [
  {
    id: 1,
    topic: "Java Basics",
    description: "Test your basic understanding of java in this test.",
    no_of_ques: 5,
    total_marks: 100
  },
  {
    id: 2,
    topic: "Spring Boot",
    description: "Test your basic understanding of Spring Boot in this test",
    no_of_ques: 10,
    total_marks: 50
  },
  {
    id: 3,
    topic: "Python",
    description: "Test your basic understanding of Python in this test",
    no_of_ques: 10,
    total_marks: 50
  },
  {
    id: 4,
    topic: "C++",
    description: "Test your understanding of C++ in this test",
    no_of_ques: 10,
    total_marks: 50
  },
]


export default function AllQuiz() {
    const classes = useStyles();
    const [firstRender, setRender] = React.useState(false)
    const [selectedIndex, setSelectedIndex] = React.useState(1);
    const [isLoaded, setIsLoaded] = React.useState(false);
    const [allQuizes, setQuizes] = React.useState();
    const [error, setError] = React.useState(false);
    useEffect(() => {
    setRender(true)
    fetch(`${baseUrl}/api/getQuiz`)
      .then(res => res.json())
      .then(
        (result) => {
          //console.log("found result.size "+result.size());
          const timer = setTimeout(() => {
          console.log('This will run after 1 second!')
          setQuizes(result.concat(Quizes));
          setIsLoaded(true);
        }, 500);

        },
        // Note: it's important to handle errors here
        // instead of a catch() block so that we don't swallow
        // exceptions from actual bugs in components.
        (error) => {
          setIsLoaded(true);
          setError(error);
        }
      )

      }, [])
      console.log("out of effect")
      console.log("isLoaded "+isLoaded)
    const handleListItemClick = (event, index) => {
        setSelectedIndex(index);
    };
    let history = useHistory();

    const handleSubmit = (event, index) => {
        setSelectedIndex(index);
          history.push({
            pathname: "/quiz",
            state: { quizId: index }
          });
    };
    const list = [1,2,3];
    if(isLoaded===false)
    {
      console.log("loading...")
      return(
        <Grid container alignItems="center"
        justifyContent="center" flexDirection= "column" xs='auto' item xs={12}>
        <Box
        minHeight="100vh"
        className="box">
          <CircularProgress role="progressbar" id="title" title="Title" />
        </Box>
        </Grid>
      )
    }
  //  debugger;
    return (
    <Grid
    container
    spacing={0}
    flexDirection= "column"
    alignItems="center"
    justifyContent="center">
       <Grid container item xs={12} m={3} className={classes.cards}>

         {
          list.map((e)=>{
            return(
           allQuizes.map((quiz)=>{
             return(
               <Card
               sx={{ minWidth: 275 }}
               variant="outlined" m={3} className={classes.root}>
               <CardMedia
                  component="img"
                  width="140"
                  height="140"
                  image={quizImg}
                  alt="green iguana"
                />
                  <CardContent>
                    <Typography style={{ fontSize: 18 }} gutterBottom>
                      {quiz.topic}
                    </Typography>
                    <Typography variant="body2" component="p">
                     {quiz.desc}
                      <br />
                      <br/>
                      <div style={{display:"flex",justifyContent: "space-between"}}>
                      <div>
                        {quiz.no_of_ques} Questions
                      </div>
                      <div>
                        {quiz.total_marks} Marks
                      </div>
                      </div>
                    </Typography>
                  </CardContent>
                  <CardActions>
                    <Button
                    variant="contained"
                    color="primary"
                    onClick={(event) => handleSubmit(event, quiz.id)}>Start</Button>
                  </CardActions>
               </Card>
             )}))
            }
         )
         }
     </Grid>
     </Grid>
    );
}
